.hero-slider {
	width: 723px;
	border-bottom: 2px solid $blue6;
	border-left: 2px solid $blue6;
	border-right: 2px solid $blue6;
	margin-left: auto;
	margin-right: auto;
	position: relative;
	margin-bottom: 36px;
	top: -13px;

	&:before, &:after {
		content: '';
		position: absolute;
		top: 0;
		background: $blue6;
		height: 2px;
		width: 193px; }
	&:before {
		left: 0; }
	&:after {
		right: 0; }
	&__blick {
		position: absolute;
		opacity: 0;
		top: 0;
		left: 0;
		width: 66px;
		height: 50px;
		margin-top: -25px;
		margin-left: -33px;
		z-index: 2; }
	&__anim-line {
		position: absolute;
		background: #6abdfd;
		z-index: 1; }
	&__tl {
		top: 0;
		left: 0;
		width: 0;
		height: 2px; }
	&__tr {
		left: calc(100% - 193px);
		top: 0;
		width: 0;
		height: 2px; }
	&__r {
		top: 0;
		right: -2px;
		height: 0;
		width: 2px; }
	&__b {
		bottom: -2px;
		right: -2px;
		width: 0;
		height: 2px; }
	&__l {
		left: -2px;
		bottom: 0;
		width: 2px;
		height: 0; }
	&__inner {
		height: 100%;
		position: relative;
		height: 155px; }
	&__slide {
		transition: all 1s;
		transform-origin: 50% 50%;
		opacity: 0;
		transform: scale(0);
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		padding: 30px 15px 0px;
		text-align: center;
		&.is-active {
			transform: scale(1);
			opacity: 1; } }
	&__title {
		font-size: 35px;
		@include play;
		color: $white;
		margin-bottom: 8px; }
	&__text {
		font-size: 19px;
		@include play;
		color: $blue8; } }
.adventages {
	font-size: 0;
	text-align: center;
	&__item {
		margin-right: 15px;
		padding: 0 5px;
		display: inline-block;
		vertical-align: top;
		width: 240px;
		&:last-of-type {
			margin-right: 0; }
		&--left {}
		&--right {} }
	&__img {
		margin-bottom: 22px;
		img {
			height: 48px;
			width: auto; } }
	&__text {
		font-size: 24px;
		color: $white;
		line-height: 1.2;
		@include play; } }
.about {
	position: relative;

	padding-top: 65px;
	padding-bottom: 60px;
	@include bigmin {
		padding-top: 100px; }
	&__bg {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: 0;
		z-index: -1;
		width: 100%;
		min-width: 1920px;
		height: 1088px;
		background-position: 50% 100%;
		background-repeat: no-repeat;
		background-size: cover;
		@include bigmin {
			height: 1160px; } }
	&__top-anim {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 48px; }
	&__title, &__hand, &__text {
		@include lgmin {
			opacity: 0; } }
	&__title {
		margin-bottom: 31px; }
	&__hand {
		position: absolute;
		top: 83px;
		right: -376px; }
	&__text {
		max-width: 710px;
		line-height: 1.25;
		letter-spacing: 0.5px;
 } }		// max-width: 643px
.asteps {
