		&--2 {
			top: 14.5%;
			left: 510px;
			svg {
				width: 74px;
				height: 62px; } }
		&--3 {
			top: 31.3%;
			left: 510px;
			svg {
				width: 74px;
				height: 62px; } }
		&--4 {
			top: 46.1%;
			left: 230px;
			width: 271px; }

		&--5 {
			top: 46.1%;
			left: 146px;
			svg {
				width: 74px;
				height: 62px; } }
		&--6 {
			top: 63.3%;
			left: 146px;
			svg {
				width: 74px;
				height: 62px; } }
		&--7 {
			top: 77.3%;
			left: 335px;
			width: 198px; } }
	&__item {
		text-align: center;
		max-width: 295px;
		display: inline-block;
		vertical-align: top;
		&--money {
			margin-right: 114px;
			margin-bottom: 105px; }
		&--graph {
			margin-bottom: 105px; }
		&--coin {
			margin-left: 201px;
			margin-right: 100px; }
		&--travel {}
		p {
			color: $blue6;
			@include play;
			font-size: 20px;
			line-height: 1.2;
			@include m {
				font-size: 15px; } } }
	&__img {
		margin-bottom: 28px;
		svg {
			fill: $blue9;
			@include trans;
			font-size: 80px; } } }
.why-we {
	position: relative;
	min-height: 496px;
	padding-top: 40px;
	&__bg {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
