		z-index: -1;
		width: 100%;
		min-width: 1924px;
		height: 641px;
		background-repeat: no-repeat;
		background-size: cover;
		// min-height: 641px
		background-position: 50% 100%; }
	&__inner {
		@include clr; }
	&__left, &__right {
		@include lgmin {
			opacity: 0; } }
	&__left {
		float: left;
		width: 68%;
		padding-right: 25px; }
	&__right {
