.plans {
	position: relative;
	padding-top: 43px;
	padding-bottom: 68px;
	&__bg {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: -288px;
		z-index: -3;
		width: 100%;
		min-width: 1920px;
		height: 1236px;
		background-position: 50% 100%;
		background-repeat: no-repeat;
		background-size: cover;
		// +bigmin
 }		// 	height: 1160px
	&__title {
		margin-bottom: 38px; }
	&__inner {
		max-width: 980px;
		margin-left: auto;
		margin-right: auto; } }

.pslide {
	width: 293px;
	max-width: 293px;
	text-align: left;
	@include trans;
	// transform-origin: 50% 50%
	background-color: $white;
	box-shadow: 3px 5.2px 10px rgba(0, 0, 0, 0.29);
	position: relative;
	&__inner {
		position: relative;
		z-index: 1;
		min-height: 557px;
		padding: 10px 21px 66px; }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 0;
		overflow: hidden;
		&:before, &:after {
			content: '';
			position: absolute;
			width: 360px;
			height: 306px;
			border-radius: 5px;
			background-color: #797a7a;
			box-shadow: 2.5px 4.3px 5px rgba(0, 0, 0, 0.24);
			transform: rotate(28deg);
			@include trans; }
		&:before {
			top: -214px;
			left: -29px; }
		&:after {
			bottom: -235px;
			left: -48px; }
		&--blue-l {
			&:before, &:after {
				background-image: linear-gradient(180deg, #328cab 0%, #03bafd 100%); } }
		&--blue-m {
			&:before, &:after {
				background-image: linear-gradient(180deg, #2cd8da 0%, #039bb2 100%); } }
		&--blue {
			&:before, &:after {
				background-image: linear-gradient(180deg, #217b99 0%, #3d489a 100%); } } }
	&__title {
		color: $white;
		@include play;
		font-size: 23px;
		text-transform: uppercase;
		text-align: center;
		position: relative;
		margin-bottom: 23px;
		&:after {
			content: '';
			position: absolute;
			bottom: -12px;
			width: 50px;
			height: 1px;
			background: $white;
			left: 50%;
			transform: translateX(-50%); } }
	&__btc, &__perc {

		color: $white;
		font-weight: 700;
		text-align: right; }
	&__btc {
		font-size: 22px;
		padding-right: 8px;
		line-height: 1;
		margin-bottom: 8px;
		@include play; }
	&__perc {
		font-size: 30px;
		padding-right: 23px;
		line-height: 1;
		margin-bottom: 55px; }
	&__line {
		@include clr;
		margin-bottom: 21px;
		&:last-of-type {
			margin-bottom: 0; } }
	&__left, &__right {
		color: $black;
		font-size: 18px;
		max-width: 60%; }
	&__left {
		float: left; }
	&__right {
		float: right;
		text-align: right; }
	&__btn {
		position: absolute;
		bottom: 14px;
		left: 20px; }
	&:hover {
		.pslide__bg {
			&:before, &:after {
				// background-color: $orange
				background-image: linear-gradient(180deg, #fd6103 0%, #fa9013 100%); } }
		.pslide__btn {
			background-color: $red; } } }
.prefooter {
	padding-top: 33px;
	&__title {
		margin-bottom: 27px; }
	&__text {
		text-align: center;
		font-size: 22px;
		color: $black;
		margin-bottom: 45px;
		line-height: 1.2;
		a {
			color: $blue2; } } }
