@mixin rob {
	font-family: 'Roboto', sans-serif; }
@mixin play {
	font-family: 'Play', sans-serif; }

@mixin bigmin {
	@media only screen and (min-width: "1921px") {
		@content; } }
@mixin lgmin {
	@media only screen and (min-width: "1171px") {
		@content; } }
@mixin lg {
	@media only screen and (max-width: "1170px") {
		@content; } }
@mixin l {
	@media only screen and (max-width: "1023px") {
		@content; } }
@mixin m {
	@media only screen and (max-width: "991px") {
		@content; } }
@mixin xs {
	@media only screen and (max-width: "767px") {
		@content; } }

$white: #fff;
$black: #00182b;
$black1: #011324;
$blue: #3f8bc6;
$blue2: #1a739f;
$blue3: #218fbf;
$blue4: #109fcc;
$blue5: #3b87c0;
$blue6: #016378;
$blue7: #2ab8bf;
$blue8: #50acf3;
$blue9: #6abdfd;
$bg-blue: #0171c5;
$bg-blue2: #005ca1;
$bg-blue3: #797a7a;
$bg-blue5: #1fc4cd;
$bg-blue6: #d5ecfc;
$gray-l: #f0f4f7;
$gray2: #cfcdcd;
$orange: #fd6103;
$orange2: #f8a035;
$orange3: #fc7309;
$green: #01ae26;
$red: #fb270b;

a {
	text-decoration: none; }
h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }
body {
	background: #FFF;
	@include rob;
	line-height: 1.4;
	color: $black;
	font-size: 18px;
	@include m {
		font-size: 14px; } }
.out {
	display: flex;
	flex-direction: column;
	overflow: hidden;
	min-height: 100vh; }
.content-block {
	flex: 1; }
.title-line {
	@include play;
	font-weight: 400;
	color: $blue2;
	margin-bottom: 34px;
	line-height: 1;
	font-size: 30px;
	@include m {
		font-size: 20px;
		margin-bottom: 20px; }
	strong {
		font-weight: normal;
		font-weight: 700; }
	i {
		display: inline-block;
		position: relative;
		bottom: 4px;
		vertical-align: bottom;
		width: 80px;
		margin-left: 8px;
		height: 2px;
		background: $blue6;
		@include m {
			display: none; } }
	&--white {
		color: $white;
		i {
			background: $white; } } }
.title-lg, .article h2 {
	color: $blue3;
	@include play;
	font-size: 24px;
	font-weight: 700;
	margin-bottom: 13px;
	line-height: 1;
	@include m {
		font-size: 18px; } }
.title-m, .article h3 {
	line-height: 1;
	color: $blue3;
	@include play;
	font-size: 22px;
	margin-bottom: 20px;
	@include m {
		font-size: 13px; } }
.article {
	color: $black;
	font-size: 18px;
	font-weight: 100;
	line-height: 1.2;
	@include m {
		font-size: 14px; }
	// margin-bottom: 50px
	a {
		color: $blue5;
		@include rob;
		font-size: 16px;
		font-weight: 100;
		text-decoration: underline;
		@include m {
			font-size: 14px; } }
	p {
		margin-bottom: 10px; }
	&>div:not(.two-col) {
		margin-bottom: 35px;
		@include m {
			margin-bottom: 20px; } }
	ul, ol {
		li {
			position: relative;
			padding-left: 22px;
			margin-bottom: 12px;
			&:last-of-type {
				margin-bottom: 0; }
			@include m {
				padding-left: 16px; } } }
	ul {
		li {
			&:before {
				content: '';
				position: absolute;
				top: 6px;
				left: 0;
				width: 9px;
				height: 9px;
				background-color: $green;
				@include m {
					top: 2px; } } } }
	ol {
		span {
			position: absolute;
			font-size: 18px;
			color: $green;
			left: 4px;
			top: 0;
			font-weight: 500;
			@include m {
				font-size: 14px; } } } }

.two-col {
	@include clr;
	margin-bottom: 25px;
	&__left, &__right {
		@include m {
			width: 100%;
			float: none; } }
	&__left {
		float: left;
		width: 58.5%;
		padding-left: 17px;
		position: relative;
		padding-right: 24px;
		@include m {
			margin-bottom: 20px; }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 4px;
			height: 35px;
			background-color: $blue6; } }
	&__right {
		float: right;
		width: 40%;
		text-align: center;
		img {
			width: 100%;
			height: auto;
			@include m {
				max-width: 465px; } } } }

blockquote {
	appearance: none;
	border: none;
	background: none;
	color: $blue4;
	@include rob;
	font-size: 18px;
	font-weight: 300;
	font-style: italic;
	padding-left: 40px;
	position: relative;
	display: block;
	margin-bottom: 17px;
	margin-top: 17px;
	@include m {
		font-size: 14px;
		padding-left: 20px;
		&:before {
			left: 0; } }
	&:before {
		content: '';
		position: absolute;
		left: 20px;
		top: 0;
		background-image: url('../img/quit.png');
		background-repeat: no-repeat;
		background-position: 50% 50%;
		width: 13px;
		height: 13px; } }
.link {
	color: $blue5;
	@include rob;
	font-size: 16px;
	font-weight: 100;
	position: relative;
	text-decoration: underline; }
.btn {
	appearance: none;
	border: 1px solid transparent;
	border-radius: 15px;
	background: transparent;
	font-size: 20px;
	@include play;
	color: $white;
	border-radius: 15px;
	padding: 7px 10px;
	min-width: 149px;
	text-align: center;
	@include trans;
	&--blue-l {
		background-color: $bg-blue5; }
	&--blue {
		background-color: $bg-blue; }
	&--border {
		border-color: $white;
		padding: 3px 10px;
		min-width: 153px;
		font-size: 24px; }
	&--red {
		background: $red;
		font-size: 24px;
		padding: 6px 10px;
		min-width: 155px;
		border-color: $red;
		&:hover {
			color: $red;
			background: $white; } } }
.styled-text {
	color: $black;
	font-weight: 100;
	padding-left: 16px;
	position: relative;
	&:before {
		content: '';
		position: absolute;
		left: 0;
		width: 4px;
		top: 6px;
		bottom: 6px;
		background: $blue6; } }
.checklist {
	font-weight: 100;
	color: $white;
	font-size: 16px;
	strong {
		font-weight: 500; }
	li {
		padding-left: 44px;
		position: relative;
		margin-bottom: 20px;
		&:last-of-type {
			margin-bottom: 0; }
		&:before, &:after {
			content: '';
			position: absolute; }
		&:before {
			left: 0;
			top: 6px;
			width: 29px;
			height: 29px;
			background: $white;
			z-index: 1; }
		&:after {
			background-image: url('../img/check.png');
			width: 27px;
			height: 27px;
			left: 3px;
			top: 2px;
			background-position: 50% 50%;
			z-index: 2; } } }
.counter {
	border-top: 2px solid $white;
	border-right: 2px solid $white;
	border-bottom: 2px solid $white;
	padding: 8px 20px 35px;
	text-align: center;
	@include play;
	&__text {
		font-size: 24px;
		color: $white;
		margin-bottom: 27px; }
	&__inner {
		font-size: 0;
		padding-left: 56px;
		span {
			width: 32px;
			height: 42px;
			line-height: 38px;
			background-color: $white;
			border: 1px solid #119cd6;
			border-radius: 3px;
			font-size: 36px;
			color: $blue2;
			display: inline-block;
			vertical-align: top;
			margin-right: 3px;
			&.counter__number-big-mr {
				margin-right: 11px; } }
		i {
			display: inline-block;
			vertical-align: bottom;
			font-size: 24px;
			color: $white;
			font-style: normal; } } }
.slick-slide {
	text-align: center; }
.slick-arrow {
	position: absolute;
	top: 50%; }
// .slick-list
// 	overflow: auto!important
// 	overflow-x: hidden!important
.prev-arrow,.next-arrow {
	transform: translateY(-50%);
	height: 40px;
	width: 24px;

	cursor: pointer;
	svg {
		font-size: 24px;
		@include trans;
		fill: $blue2; }
	&:hover {
		svg {
			fill: $blue6; } } }
.prev-arrow {
	left: -30px; }
.next-arrow {
	right: -30px; }
