.cab {
	&__links-line {
		font-size: 0;
		text-align: center;
		margin-bottom: 42px;
		@include m {
			margin-bottom: 20px; }
		a {
			color: $bg-blue;
			@include play;
			font-size: 30px;
			display: inline-block;
			vertical-align: top;
			height: 55px;
			min-width: 195px;
			margin-right: 8px;
			background: $white;
			border: 1px solid $bg-blue;
			border-radius: 4px;
			transform: skew(25deg);
			@include trans;
			@include m {
				font-size: 16px;
				min-width: 130px;
				height: 30px;
				margin-bottom: 10px; }
			span {
				line-height: 50px;
				transform: skew(-25deg);
				display: inline-block;
				vertical-align: top;
				@include m {
					line-height: 30px; } }
			&:last-of-type {
				margin-right: 0; }
			&.is-active {
				color: $white;
				background: $bg-blue; }
			&:hover {
				background: $bg-blue;
				color: $white; } } }
	&__profit {
		margin-bottom: 80px;
		@include clr;
		background: $bg-blue;
		color: $white;
		font-size: 24px;
		min-height: 68px;
		@include play;
		@include m {
			margin-bottom: 40px;
			min-height: 40px;
			font-size: 16px; }
		@include xs {
			background: transparent;
			font-size: 14px; } }
	&__profit-left {
		float: left;
		padding-left: 15px;
		padding-top: 11px;
		@include m {
			padding-top: 4px; }
		@include xs {
			float: none;
			background: $bg-blue;
			margin-bottom: 10px;
			text-align: center;
			padding-left: 5px;
			padding-right: 5px; }
		span {
			display: inline-block;
			vertical-align: top;
			line-height: 48px;
			@include m {
				line-height: 32px; }
			@include xs {
				line-height: 20px; } }
		i {
			margin-right: 32px;
			@include m {
				margin-right: 15px; } }
		svg {
			font-size: 44px;
			fill: $white;
			@include m {
				font-size: 30px; }
			@include xs {
				font-size: 20px; } } }
	&__profit-right {
		float: right;
		font-size: 0;
		@include xs {
			float: none;
			text-align: center; }
		span {
			display: inline-block;
			vertical-align: top;
			line-height: 68px;
			@include m {
				line-height: 40px; } } }
	&__profit-cash-name {
		font-size: 24px;
		min-width: 68px;
		text-align: center;
		@include xs {
			background: $bg-blue;
			font-size: 16px;
			min-width: 40px; } }
	&__counter {
		display: inline-block;
		vertical-align: top;
		padding: 0 9px;
		background: white;
		div {
			font-size: 48px;
			font-weight: 700;
			width: 51px;
			background: $bg-blue;
			margin-right: 5px;
			text-align: center;
			position: relative;
			display: inline-block;
			vertical-align: top;
			@include m {
				font-size: 32px;
				width: 40px; }
			@include xs {
				font-size: 20px;
				width: 	30px; }
			&:last-of-type {
				margin-right: 0; }
			&:before, &:after {
				content: '';
				position: absolute;
				top: 50%;
				width: 100%;
				left: 0; }
			&:before {
				background-color: #014376;
				height: 1px;
				z-index: 2; }
			&:after {
				content: '';
				height: 20px;
				z-index: 0;
				background-image: linear-gradient(to top, #0171c5 0%, #005ca1 100%); } }
		span {
			position: relative;
			z-index: 1; } }
	&__blocks {
		font-size: 0;
		margin-bottom: 76px;
		@include m {
			margin-bottom: 20px; }
		.cblock {
			margin-right: 31px;
			@include m {
				margin-left: auto;
				margin-right: auto;
				margin-bottom: 20px; }
			&:last-of-type {
				margin-right: 0;
				@include m {
					margin-right: auto; } } } } }
.cblock {
	width: 457px;
	min-height: 278px;
	display: inline-block;
	vertical-align: top;
	background-color: $white;
	box-shadow: 3px 5.2px 10px rgba(0, 0, 0, 0.29);
	position: relative;
	padding: 16px 18px 28px;
	@include m {
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 20px; }
	@include xs {
		width: 100%;
		text-align: center; }
	&__bg {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		overflow: hidden;
		@include xs {
			display: none; }
		&:before {
			content: '';
			position: absolute;
			z-index: 1;
			top: -318px;
			left: -31px;
			width: 513px;
			height: 380px;
			box-shadow: 2.5px 4.3px 5px rgba(0, 0, 0, 0.24);
			border-radius: 20px;
			transform-origin: 50% 50%;
			transform: rotate(16deg); } }
	&__inner {
		position: relative;
		z-index: 2; }
	&__title {
		text-align: right;
		font-size: 24px;
		color: $white;
		@include play;
		text-transform: uppercase;
		padding-right: 12px;
		margin-bottom: 27px;
		@include xs {
			color: $blue;
			font-size: 18px;
			padding-right: 0;
			text-align: center; }
		span {
			position: relative;
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				width: 51px;
				height: 1px;
				background-color: $white;
				bottom: -12px;
				@include xs {
					display: none; } } } }
	&__aft-title-line {
		margin-bottom: 32px;
		&>span {
			font-size: 18px;
			color: $black;
			display: inline-block;
			vertical-align: top;
			margin-right: 12px; } }
	&__title-sec {
		color: $bg-blue;
		font-size: 18px;
		margin-bottom: 16px; }
	&__two-col {
		@include clr;
		padding-left: 30px;
		padding-right: 25px;
		@include xs {
			padding: 0;
			margin-bottom: 15px; } }
	&__left, &__right {
		width: 50%;
		@include xs {
			width: 100%;
			float: none; } }
	&__left {
		float: left; }
	&__right {
		float: right;
		padding-left: 51px;
		&--pad-sm {
			padding-left: 23px; }
		@include xs {
			padding: 0; } }
	&__value {
		font-size: 18px;
		color: $black;
		margin-bottom: 14px; }
	&__field {
		input {
			width: 100%;
			height: 28px;
			line-height: 28px;
			font-size: 18px;
			background-color: $white;
			border: 1px solid $gray2;
			padding: 0 9px; }
		@include xs {
			max-width: 200px;
			margin-left: auto;
			margin-right: auto; } }
	&__btns {
		padding: 0 17px 0 22px;
		@include clr;
		@include xs {
			padding: 0; }

		&-left {
			float: left;
			@include xs {
				float: none;
				margin: 10px; } }
		&-right {
			float: right;
			@include xs {
				float: none;
				margin: 10px; } } }
	&--blue {
		.cblock__bg {
			&:before {
				background-color: $bg-blue3;
				background-image: linear-gradient(180deg, #217b99 0%, #3d489a 100%); } } }
	&--blue-l {
		.cblock__bg {
			&:before {
				background-color: $bg-blue3;
				background-image: linear-gradient(180deg, #2cd8da 0%, #039bb2 100%); } } } }
