.footer {
	@include cover;
	position: relative;
	padding-top: 16px;
	padding-bottom: 20px;
	margin-top: 50px;
	min-height: 214px;
	&:before {
		content: '';
		position: absolute;
		left: 0;
		width: 100%;
		top: 0;
		height: 100%;
		background: #133045;
		z-index: -3; }
	@include m {
		background-image: url('../img/footer-bg.jpg');
		@include cover; }
	&__bg {
		position: absolute;
		width: 100%;
		left: 50%;
		transform: translateX(-50%);
		bottom: 0;
		z-index: -2;
		width: 100%;
		min-width: 1920px;
		height: 505px;
		background-size: cover;
		background-position: 50% 100%;
		background-repeat: no-repeat;
		@include lg {
			height: 100%;
			background-position: 50% 110%; } }
	&__inner {
		display: flex;
		@include m {
			display: block; } }
	&__left {
		flex-basis: 30%;
		flex-shrink: 0;
		padding-top: 13px;
		@include m {
			text-align: center;
			margin-bottom: 15px; } }
	&__right {
		flex-basis: 70%;
		flex-grow: 1; }
	&__logo {
		display: block;
		margin-bottom: 13px;
		@include m {
			display: inline-block;
			vertical-align: top;
			img {
				width: 180px;
				height: auto; } } }
	&__copy {
		display: block; }
	&__top-line {
		@include clr;
		margin-bottom: 33px; }
	&__nav {
		float: left;
		font-size: 0;
		@include m {
			float: none;
			text-align: center;
			margin-bottom: 10px; }
		a {
			font-size: 20px;
			@include play;
			color: $white;
			position: relative;
			@include trans;
			@include m {
				padding: 5px;
				font-size: 14px; }
			&:hover {
				color: $orange3; } }
		li {
			display: inline-block;
			vertical-align: top;
			margin-right: 52px;
			&:last-of-type {
				margin-right: 0; }
			@include m {
				margin-right: 0; }
			&.is-active {
				a {
					color: $orange3;
					&:after {
						content: '';
						position: absolute;
						width: 100%;
						height: 1px;
						background: $orange3;
						bottom: -7px;
						left: 0;
						@include m {
							display: none; } } } } } }

	&__email {
		float: right;
		font-size: 20px;
		@include play;
		color: $white;
		@include m {
			font-size: 14px;
			text-align: center;
			float: none;
			display: block; } }
	&__text {
		padding-left: 50px;
		// margin-bottom: 15px
		@include clr;
		@include m {
			padding-left: 0; }
		p {
			color: $blue5;
			font-size: 16px;
			font-weight: 100;
			line-height: 1.2; } }
	&__title {
		color: $blue5;
		font-size: 16px;
		font-weight: 500;
		margin-bottom: 3px; }
	&__more {
		margin-top: 11px;
		float: right; } }
